import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';
import { Constants } from '../helpers/constant';
import { MessageService } from 'primeng/api';
import { BaseComponent } from '../components/base/base.component';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.sass'],
})
export class LoginComponent extends BaseComponent implements OnInit {
  loginForm: FormGroup;
  submitted: boolean = false;
  isLoadingDone: boolean = true;

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private authService: AuthenticationService,
    public messageService: MessageService
  ) {
    super(messageService);
  }

  ngOnInit(): void {
    this.loginForm = this.formBuilder.group({
      username: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required],
    });
  }

  get getFormControls() {
    return this.loginForm.controls;
  }

  loginFormSubmit():void {
    this.submitted = true;
    this.isLoadingDone = false;
    if (this.loginForm.invalid) { this.isLoadingDone = true; return;}

    const username = this.loginForm.get('username').value;
    const password = this.loginForm.get('password').value;

      this.authService.login(username, password).subscribe(
        (responseData) => {    
          if (responseData.message === "authenticated") {
            this.isLoadingDone = true;
            this.resetLoginControls();
            localStorage.setItem('email', username);
            this.router.navigateByUrl('/dashboard');
          } else if (responseData.message === "not authenticated") {
            this.isLoadingDone = true;
            this.resetLoginControls();
            this.updateToastMessage(Constants.invalidLogin, Constants.error, "Login");
          }
        }, 
        (err) => {
          if (err.status === 401) {
            console.log(err);
            this.isLoadingDone = true;
            this.resetLoginControls();
            this.updateToastMessage(Constants.invalidLogin, Constants.error, "Login");
          }
        }
      );
  }

  resetLoginControls(){
    this.loginForm.reset(); 
    this.submitted = false;
  }
}
