import { Component, OnInit, Inject } from '@angular/core';
import { DialogOverviewExampleDialog } from './assistant.modal.component';
import { ProfileService } from '../services/profile.service';
import { AssistantInformation } from '../models/assistantinformation';
import { Constants } from '../helpers/constant';
import { MessageService } from 'primeng/api';
import { BaseComponent } from '../components/base/base.component';

@Component({
  selector: 'app-usermanagement',
  templateUrl: './usermanagement.component.html',
  styleUrls: ['./usermanagement.component.sass'],
})
export class UsermanagementComponent extends BaseComponent implements OnInit {
  gridDatas: Array<AssistantInformation> = [];
  isLoadingDone: boolean = true;
  loadModal:boolean = false;
  selectedRecordId:number;
  loadAddEditModal:boolean = false;
  dialog_title: string = '';
  isEdit:boolean = false;

  constructor(
    private profileService: ProfileService,
    public messageService: MessageService
  ) {
    super(messageService);
  }

  ngOnInit(): void {
    this.getAsstProfileInfo();
  }

  openDialog(): void {
    this.dialog_title = "Add Assistant Information";
    this.loadAddEditModal = true;
    this.selectedRecordId = 0;
    this.isEdit = false;
  }

  getAsstProfileInfo() {
    this.isLoadingDone = true;
    const masterEmail = localStorage.getItem('email');
    this.profileService
      .getAssistantInformation(masterEmail)
      .subscribe((asstinfo) => {
        this.gridDatas = asstinfo;
        this.isLoadingDone = false;
      });
  }

  deleteAssistant(id) {
    this.selectedRecordId = id;
    this.loadModal = true; 
  }

  dialogButtonClickEvent(selectedButton) {
    if(selectedButton === 'yes'){
      if(this.selectedRecordId !== 0) {
        this.profileService.deleteAssistantRecord(this.selectedRecordId).subscribe((res) => {
          if (res === 'Slave Information Deleted Successfully') {
             this.getAsstProfileInfo();
             this.selectedRecordId = 0;
             this.loadModal = false;
             this.updateToastMessage(Constants.genericMessage, Constants.success, "Assistant Information");
           }
        });
      }
    } else { this.loadModal = false;}
  }

  handleSureDialogResult($event) {
    if($event == 'close'){
      this.loadAddEditModal = false;
    } else if($event == 'success'){
      this.loadAddEditModal = false;
      this.updateToastMessage(Constants.genericMessage, Constants.success, "Assistant Information");
    } else { 
        this.loadAddEditModal = false;
        this.updateToastMessage(Constants.serverSideErrr, Constants.error, "Assistant Information");
    }
  }

  editAssistant(id) {
    this.dialog_title = "Edit Assistant Information";
    this.loadAddEditModal = true;
    this.isEdit = true;
    this.selectedRecordId = id;
  }
}
