import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { UrlMappers } from '../helpers/urlmappers';

@Injectable()
export class AuthenticationService {
  constructor(private http: HttpClient, private router: Router) {}

  // getToken(username: string, password: string): Observable<any> {
  //   const url =
  //     UrlMappers.encodeUrl + '?name1=' + username + '&name2=' + password;
  //   return this.http.get<string>(url).pipe(
  //     map((a) => {
  //       return a;
  //     }),
  //     catchError((err) => {
  //       throw err;
  //     })
  //   );
  // }

  login(username:string, password:string): Observable<any> {
    debugger;
    const loginurl = UrlMappers.loginUrl;
    const content = {
      "username":username,
      "password": password
    };
    return this.http
      .post<any>(loginurl, content)
      .pipe(
        map((data) => {
          return data;
        }),
        catchError((e) => {
          console.log(e);
          throw e;
        })
      );
  }

  public get getLoggedInStatus() {
    return localStorage.getItem('email') !== null ? true : false;
  }

  forgetPassword(email: string): Observable<string> {
    const url = UrlMappers.forgetPassword + '?Email=' + email;
    return this.http
      .post<string>(url, '')
      .pipe(
        map((data) => {
          return data;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl('/login');
  }
}
