import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthenticationService } from '../services/authentication.service';
import { UrlMappers } from '../helpers/urlmappers';
import { Router } from '@angular/router';
import { Constants } from '../helpers/constant';
import { MessageService } from 'primeng/api';
import { BaseComponent } from '../components/base/base.component';

@Component({
  selector: 'app-forgetpassword',
  templateUrl: './forgetpassword.component.html',
  styleUrls: ['./forgetpassword.component.sass'],
})
export class ForgetpasswordComponent extends BaseComponent implements OnInit {
  forgetPasswordForm: FormGroup;
  submitted: boolean = false;
  isforgetLoadDone: boolean = true;

  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthenticationService,
    private router: Router,
    public messageService: MessageService
  ) {
    super(messageService);
  }

  ngOnInit(): void {
    this.forgetPasswordForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
    });
  }

  get getFormControls() {
    return this.forgetPasswordForm.controls;
  }

  forgetPasswordFormSubmit() {
    this.submitted = true;
    this.isforgetLoadDone = false;
    if (this.forgetPasswordForm.invalid) {
      this.isforgetLoadDone = true;
      return;
    }

    const email = this.getFormControls['email'].value;
    // this.authService
    //   .getToken(
    //     UrlMappers.forgetPasswordUsername,
    //     UrlMappers.forgetPasswordPassword
    //   )
    //   .subscribe((responseToken) => {
        //let token = responseToken.split(':')[1].trim();
        this.authService.forgetPassword(email).subscribe(
          (res) => {
            if (res === 'Success') {
              this.isforgetLoadDone = true;
              this.resetFormControls();
               this.updateToastMessage(Constants.forgetPasswordSuccess, Constants.success ,"Forget Password");
            }
          },
          (err) => {
            if (err.staus === 401) {
              this.isforgetLoadDone = true;
              this.updateToastMessage(Constants.invalidEmail, Constants.error ,"Forget Password");
              this.resetFormControls();
            } else {
              this.isforgetLoadDone = true;
              this.updateToastMessage(Constants.serverSideErrr, Constants.error ,"Forget Password");
              this.resetFormControls();
            }
          }
        );
      //});
  }

  resetFormControls() {
    this.submitted = false;
    this.forgetPasswordForm.reset();
  }
}
