import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, map } from 'rxjs/operators';
import { PersonalInformation } from '../models/personalinformation';
import { AssistantInformation } from '../models/assistantinformation';
import { UrlMappers } from '../helpers/urlmappers';

@Injectable()
export class ProfileService {
  static headers: any = {
    Authorization: 'Basic ' + localStorage.getItem('token'),
  };

  constructor(private http: HttpClient) {}

  getProfileInformation(email: string): Observable<PersonalInformation> {
    const url = UrlMappers.getProfile + '?emailid=' + email;
    return this.http
      .get<PersonalInformation>(url, { headers: ProfileService.headers })
      .pipe(
        map((profile) => {
          return profile;
        }),
        catchError((err) => {
          throw err;
        })
      );
  }

  updateProfileInformation(
    profileData: PersonalInformation
  ): Observable<PersonalInformation> {
    const url =
      UrlMappers.updateProfile +
      '?Name=' +
      profileData.Name +
      '&Mobile=' +
      profileData.Mobile +
      '&Email=' +
      profileData.Email +
      '&Address=' +
      profileData.Address +
      '&GST=' +
      profileData.GST +
      '&Company=' +
      profileData.Company;

    return this.http
      .post<PersonalInformation>(url, '', { headers: ProfileService.headers })
      .pipe(
        map((response) => {
          return response;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  updatePassword(oldPassword: string, newPassword: string): Observable<string> {
    const url =
      UrlMappers.resetPasswordUrl +
      '?Email=' +
      localStorage.getItem('email') +
      '&Password=' +
      oldPassword +
      '&Temp=' +
      newPassword;

    return this.http
      .post<string>(url, '', { headers: ProfileService.headers })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  getAssistantInformation(
    email: string
  ): Observable<Array<AssistantInformation>> {
    const url = UrlMappers.getAssistantProfile + '?email=' + email;
    return this.http
      .get<Array<AssistantInformation>>(url, {
        headers: ProfileService.headers,
      })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  addAssistantInformation(asstInfo: AssistantInformation): Observable<any> {
    const url =
      UrlMappers.addAssistantProfile +
      '?MasterEmail=' +
      asstInfo.MasterEmail +
      '&Password=' +
      asstInfo.Password +
      '&IsDashboard=' +
      asstInfo.IsDashboard +
      '&UserName=' +
      asstInfo.UserName +
      '&IsQuotation=' +
      asstInfo.IsQuotation +
      '&IsProduct=' +
      asstInfo.IsProduct +
      '&IsSales=' +
      asstInfo.IsSales +
      '&IsReminder=' +
      asstInfo.IsReminder +
      '&IsReports=' +
      asstInfo.IsReports +
      '&IsSettings=' +
      asstInfo.IsSettings +
      '&IsInvoice=' +
      asstInfo.IsInvoice;

    return this.http
      .post<AssistantInformation>(url, '', { headers: ProfileService.headers })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  deleteAssistantRecord(id: number): Observable<string> {
    const url = UrlMappers.deleteSlaveDetails + '?id=' + id;
    return this.http
      .post<string>(url, '', { headers: ProfileService.headers })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  getAssistantRecordById(id: number): Observable<AssistantInformation> {
    const url = UrlMappers.getAssistantProfileById + '?id=' + id;
    return this.http
      .get<AssistantInformation>(url, { headers: ProfileService.headers })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  updateAssistantRecordById(asstDetails: AssistantInformation) {
    const url =
      UrlMappers.updateAssistantProfile +
      '?id=' +
      asstDetails.ID +
      '&MasterEmail=' +
      asstDetails.MasterEmail +
      '&Password=' +
      asstDetails.Password +
      '&IsDashboard=' +
      asstDetails.IsDashboard +
      '&UserName=' +
      asstDetails.UserName +
      '&IsQuotation=' +
      asstDetails.IsQuotation +
      '&IsProduct=' +
      asstDetails.IsProduct +
      '&IsSales=' +
      asstDetails.IsSales +
      '&IsReminder=' +
      asstDetails.IsReminder +
      '&IsReports=' +
      asstDetails.IsReports +
      '&IsSettings=' +
      asstDetails.IsSettings +
      '&IsInvoice=' +
      asstDetails.IsInvoice;

    return this.http
      .post<AssistantInformation>(url, '', { headers: ProfileService.headers })
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  uploadImage(imageFile): Observable<any> {
    debugger;
    const url = UrlMappers.uploadImage;
    
    return this.http
      .post<File>('./assets/images/logo', imageFile)
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }

  getImage(imageName){
    debugger;
    const url = UrlMappers.downloadImage+'?imagename='+'download.jpg';
    return this.http
      .post<any>(url, '', { headers: ProfileService.headers, responseType: 'blob' as 'json'})
      .pipe(
        map((a) => {
          return a;
        }),
        catchError((e) => {
          throw e;
        })
      );
  }
}
