import { Inject, Component, Input, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ProfileService } from '../services/profile.service';
import { AssistantInformation } from '../models/assistantinformation';

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: './assitant.modaltemplate.component.html',
  styleUrls: ['./usermanagement.component.sass'],
})
export class DialogOverviewExampleDialog {
  assistantForm: FormGroup;
  assistantFormSubmitted: boolean = false;
  selectedFeatures: Array<string> = [];
  ischkBoxError: boolean = false;

  @Input('isEdit')
  isEdit: boolean = false;
  @Input('title')
  title: string = '';
  @Input('selectedRecordId')
  selectedRecordId:number;
  @Output('emitData')
  emitData = new EventEmitter<string>();

  constructor(
    private profileService: ProfileService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.assistantForm = this.initAssistantFormControls();
  }

  ngOnChanges(changes: SimpleChanges) {
    this.getAssistantRecordById(this.selectedRecordId);
  }

  initAssistantFormControls() {
    return this.formBuilder.group({
      asstusername: ['', [Validators.required, Validators.email]],
      asstpassword: ['', Validators.required],
      dashboard: [false, Validators.requiredTrue],
      product: [false, Validators.requiredTrue],
      sales: [false, Validators.requiredTrue],
      reports: [false, Validators.requiredTrue],
      quotation: [false, Validators.requiredTrue],
      invoice: [false, Validators.requiredTrue],
      reminder: [false, Validators.requiredTrue],
      settings: [false, Validators.requiredTrue],
    });
  }

  get getAssistantFormControls() {
    return this.assistantForm.controls;
  }

  assistantFormSubmit() {
    this.assistantFormSubmitted = true;
    if (this.selectedFeatures.length === 0) {
      this.ischkBoxError = true;
      setTimeout(() => {
        this.ischkBoxError = false;
      }, 2000);
    } else {
      const asstProfile = new AssistantInformation();
      const asstControl = this.getAssistantFormControls;
      asstProfile.MasterEmail = localStorage.getItem('email');
      asstProfile.UserName = asstControl['asstusername'].value;
      asstProfile.Password = asstControl['asstpassword'].value;

      if (this.selectedFeatures.indexOf('dashboard') !== -1)
        asstProfile.IsDashboard = true;
      else asstProfile.IsDashboard = false;
      if (this.selectedFeatures.indexOf('product') !== -1)
        asstProfile.IsProduct = true;
      else asstProfile.IsProduct = false;
      if (this.selectedFeatures.indexOf('sales') !== -1)
        asstProfile.IsSales = true;
      else asstProfile.IsSales = false;
      if (this.selectedFeatures.indexOf('reports') !== -1)
        asstProfile.IsReports = true;
      else asstProfile.IsReports = false;
      if (this.selectedFeatures.indexOf('quotation') !== -1)
        asstProfile.IsQuotation = true;
      else asstProfile.IsQuotation = false;
      if (this.selectedFeatures.indexOf('invoice') !== -1)
        asstProfile.IsInvoice = true;
      else asstProfile.IsInvoice = false;
      if (this.selectedFeatures.indexOf('reminder') !== -1)
        asstProfile.IsReminder = true;
      else asstProfile.IsReminder = false;
      if (this.selectedFeatures.indexOf('settings') !== -1)
        asstProfile.IsSettings = true;
      else asstProfile.IsSettings = false;

      this.profileService
        .addAssistantInformation(asstProfile)
        .subscribe((response) => {
          if (response != null) {

            this.emitData.emit("success");
          }
        }), (err => {
          this.emitData.emit("error");
        });
      this.resetFormControls();
    }
  }

  resetFormControls() {
    this.assistantForm.reset();
    this.assistantFormSubmitted = false;
  }

  onCheckboxChange(event) {
    event.preventDefault();
    if (event.target.checked) {
      if (!this.selectedFeatures.includes(event.target.value))
        this.selectedFeatures.push(event.target.value);
    } else {
      if (this.selectedFeatures.includes(event.target.value)) {
        const index: number = this.selectedFeatures.indexOf(event.target.value);
        if (index !== -1) {
          this.selectedFeatures.splice(index, 1);
        }
      }
    }
  }

  getAssistantRecordById(id:number): void {
    if(id > 0) {
      this.resetFormControls();
      this.profileService
      .getAssistantRecordById(this.selectedRecordId)
      .subscribe((assistInfo) => {
        const controls = this.getAssistantFormControls;
        controls['asstpassword'].setValue(assistInfo.Password);
        controls['asstusername'].setValue(assistInfo.UserName);

        if (assistInfo.IsDashboard) {
          controls['dashboard'].setValue(true);
          this.selectedFeatures.push('dashboard');
        } else controls['dashboard'].setValue(false);

        if (assistInfo.IsProduct) {
          controls['product'].setValue(true);
          this.selectedFeatures.push('product');
        } else controls['product'].setValue(false);

        if (assistInfo.IsSales) {
          controls['sales'].setValue(true);
          this.selectedFeatures.push('sales');
        } else controls['sales'].setValue(false);

        if (assistInfo.IsQuotation) {
          controls['quotation'].setValue(true);
          this.selectedFeatures.push('quotation');
        } else controls['quotation'].setValue(false);

        if (assistInfo.IsReports) {
          controls['reports'].setValue(true);
          this.selectedFeatures.push('reports');
        } else controls['reports'].setValue(false);

        if (assistInfo.IsInvoice) {
          controls['invoice'].setValue(true);
          this.selectedFeatures.push('invoice');
        } else controls['invoice'].setValue(false);

        if (assistInfo.IsReminder) {
          controls['reminder'].setValue(true);
          this.selectedFeatures.push('reminder');
        } else controls['reminder'].setValue(false);

        if (assistInfo.IsSettings) {
          controls['settings'].setValue(true);
          this.selectedFeatures.push('settings');
        } else controls['settings'].setValue(false);
      });
    }
  }

  updateAssistantRecordById() {
    const asstProfile = new AssistantInformation();
    const asstControl = this.getAssistantFormControls;
    asstProfile.ID = this.selectedRecordId;
    asstProfile.MasterEmail = localStorage.getItem('email');
    asstProfile.UserName = asstControl['asstusername'].value;
    asstProfile.Password = asstControl['asstpassword'].value;

    if (this.selectedFeatures.indexOf('dashboard') !== -1)
      asstProfile.IsDashboard = true;
    else asstProfile.IsDashboard = false;
    if (this.selectedFeatures.indexOf('product') !== -1)
      asstProfile.IsProduct = true;
    else asstProfile.IsProduct = false;
    if (this.selectedFeatures.indexOf('sales') !== -1)
      asstProfile.IsSales = true;
    else asstProfile.IsSales = false;
    if (this.selectedFeatures.indexOf('reports') !== -1)
      asstProfile.IsReports = true;
    else asstProfile.IsReports = false;
    if (this.selectedFeatures.indexOf('quotation') !== -1)
      asstProfile.IsQuotation = true;
    else asstProfile.IsQuotation = false;
    if (this.selectedFeatures.indexOf('invoice') !== -1)
      asstProfile.IsInvoice = true;
    else asstProfile.IsInvoice = false;
    if (this.selectedFeatures.indexOf('reminder') !== -1)
      asstProfile.IsReminder = true;
    else asstProfile.IsReminder = false;
    if (this.selectedFeatures.indexOf('settings') !== -1)
      asstProfile.IsSettings = true;
    else asstProfile.IsSettings = false;

    this.profileService
      .updateAssistantRecordById(asstProfile)
      .subscribe((a) => {
        if (a != null) {
          this.selectedRecordId = 0;
          this.emitData.emit("success");
        }
      }), (err => {
        this.emitData.emit("error");
      });
    this.resetFormControls();
  }

  closeModal() {
    this.resetFormControls() ;
    this.emitData.emit("close");
  }
}
